import React from "react";
import { NavLink } from "react-router-dom";

import "./Skill.scss";
export default function Skill() {
     return (
          <div className="skill__container">
               <div className="skill__avatar">
                    <NavLink
                         exact
                         activeClassName="active1"
                         to="/kythuat"
                         className="skill__button"
                    >
                         <i className="fas fa-user-circle"></i>{" "}
                         <span>Avatar - Cover qua các năm</span>
                    </NavLink>
                    <NavLink
                         activeClassName="active1"
                         to="/kythuat/logo"
                         className="skill__button"
                    >
                         <i className="fas fa-leaf"></i> <span>Logo PNG</span>
                    </NavLink>

                    <NavLink
                         exact
                         activeClassName="active1"
                         to="/kythuat/webs"
                         className="skill__button"
                    >
                         <i className="fas fa-globe"></i>{" "}
                         <span>Các web lấy hình</span>
                    </NavLink>
                    <NavLink
                         exact
                         activeClassName="active1"
                         to="/"
                         className="skill__button"
                    >
                         <i className="fas fa-file-image"></i>{" "}
                         <span>Học Photoshop </span>
                    </NavLink>
               </div>
          </div>
     );
}
